@import '~normalize.css';

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/Roboto/Regular/Roboto-Regular.woff") format("woff"),
    url("./assets/fonts/Roboto/Regular/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Circe";
  src: url("./assets/fonts/Circe/ExtraBold/Circe-ExtraBold.woff") format("woff"),
    url("./assets/fonts/Circe/ExtraBold/Circe-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: "Circe";
  src: url("./assets/fonts/Circe/Bold/Circe-Bold.woff") format("woff"),
    url("./assets/fonts/Circe/Bold/Circe-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Circe";
  src: url("./assets/fonts/Circe/Regular/Circe-Regular.woff") format("woff"),
    url("./assets/fonts/Circe/Regular/Circe-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Circe";
  src: url("./assets/fonts/Circe/Light/Circe-Light.woff") format("woff"),
    url("./assets/fonts/Circe/Light/Circe-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "Circe Light";
  src: url("./assets/fonts/Circe/Circe-Thin.ttf") format("truetype");
  font-weight: 100;
  font-style: normal;
}

body {
  font-family: 'Circe', sans-serif;
  font-weight: 400;
}
